/**
 * Utils
 */
class Utils {

  static CHROME_BROWSER  = 'CHROME_BROWSER';
  static FIREFOX_BROWSER = 'FIREFOX_BROWSER';
  static EDGE_BROWSER    = 'EDGE_BROWSER';

  constructor() {
    let isIE = /*@cc_on!@*/false || !!document.documentMode;
    let isEdge = !isIE && !!window.StyleMedia;

    if (navigator.userAgent.indexOf('Chrome') !== -1 && !isEdge) {
      this._browser = Utils.CHROME_BROWSER;
    }
    else if(navigator.userAgent.indexOf('Firefox') !== -1 ) {
      this._browser = Utils.FIREFOX_BROWSER;
    }
    else {
      this._browser = Utils.EDGE_BROWSER;
    }
  }

  convertSecondsToMinutes(seconds) {
    return {
      hours: Math.floor(seconds / 3600),
      minutes: Math.floor(seconds % 3600 / 60),
      seconds: Math.floor(seconds % 3600 % 60),
    };
  };

  isBrowser(browser) {
    return this._browser === browser;
  };

  isChromeBrowser() {
    return this.isBrowser(Utils.CHROME_BROWSER);
  };

  isFirefoxBrowser() {
    return this.isBrowser(Utils.FIREFOX_BROWSER);
  };

  isEdgeBrowser() {
    return this.isBrowser(Utils.EDGE_BROWSER);
  };

  randomNumber(from, to) {
    let rand = parseInt(Math.floor(Math.random()*(to-from+1)+from));
    return rand;
  };

  generateGUID() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }
    return s4() + s4() + s4() + s4() + s4() + s4() + s4() + s4();
  };

  buildPathForCountryFlag(countryCode) {
    return chrome.runtime.getURL('flags/4x3/' + countryCode.toLowerCase() + '.svg')
  };

  timeLeftString(seconds) {
    if (seconds < 60) {
      return seconds.toString();
    }

    let date = this.convertSecondsToMinutes(seconds);
    let minutesText = `${date.minutes}`;
    let secondsText = `${date.seconds}`.padStart(2, 0);
    let time = date.minutes > 10 ? `${minutesText}m` : `${minutesText}:${secondsText}`;
    return time;

  };

}


export default Utils;