import LoggerService from '../services/Logger/LoggerService';
var loggerService = new LoggerService();
import CoreDispatcherService from '../core/Services/Dispatcher/CoreDispatcherService';
import AuthService from '../services/Auth/AuthService';
import BaseProxyService from '../services/ProxyService/BaseProxyService';
import CacheService from '../services/CacheService';
import DataCenterAuthService from '../services/DataCenter/DataCenterAuthService';
import DataCenterSyncProxyListService from '../services/DataCenter/Sync/DataCenterSyncProxyListService';
import DeviceService from '../services/DeviceService';
import MessageDispatcherService from '../services/MessageDispatcherService';
import ResidentialAuthService from '../services/Residential/ResidentialAuthService';
import ResidentialSyncProxyListService from '../services/Residential/ResidentialSyncProxyListService';
import SettingsService from '../services/SettingsService/SettingsService';
import StatisticsService from '../services/StatisticsService';
import StorageService from '../services/StorageService/StorageService';
import SyncService from '../services/Sync/SyncService';
import TabService from '../services/TabService';
import TimerService from '../services/TimerService';
import ToolbarIconService from '../services/ToolbarIconService';
import AccountStorage from '../storages/AccountStorage';
import SettingsStorage from '../services/SettingsService/Storages/SettingsStorage';
import LocationMapper from '../utils/LocationMapper';
import Core from '../core/Core';
import CoreConstant from '../core/CoreConstant';
import AppConstant from './constans';
import LocationService from '../services/LocationService/LocationService';
import LocationConfiguration from '../services/LocationService/LocationConfiguration';
import ProxyCredential from '../services/ProxyService/Models/ProxyCredential';
import LocationCommonService from '../services/LocationService/LocationCommonService';
import CredentialServiceProvider from '../services/CredentialServiceProvider/CredentialServiceProvider';
import NumberHelper from '../utils/NumberHelper';
import ResidentialCredentialServiceProvider from '../services/Residential/ResidentialCredentialServiceProvider';
import LocationHistoryStackStorage from '../storages/LocationHistoryStackStorage/LocationHistoryStackStorage';
import IpInfoService from '../services/IpInfoService/IpInfoService';
import IpInfoMapper from '../mappers/IpInfoMapper';
import GeoService from '../services/GeoService/GeoService';
var dispatcherService = new CoreDispatcherService(loggerService);
var locationMapper = new LocationMapper();
var messageDispatcherService = new MessageDispatcherService(loggerService);
var storageService = new StorageService();
var settingsStorage = new SettingsStorage(storageService);
var accountStorage = new AccountStorage(storageService);
var locationHistoryStachStorage = new LocationHistoryStackStorage(storageService);
var timerService = new TimerService();
var tabService = new TabService();
var settingsService = new SettingsService(settingsStorage);
var statisticsService = new StatisticsService();
var cacheService = new CacheService();
var toolbarIconService = new ToolbarIconService();
var dataCenterAuthService = new DataCenterAuthService();
var residentialAuthService = new ResidentialAuthService();
var authCommonService = new AuthService(accountStorage);
var deviceService = new DeviceService();
var baseProxyService = new BaseProxyService(settingsService, cacheService);
var residentialCredentialServiceProvider = new ResidentialCredentialServiceProvider(function (location) {
    var guid = NumberHelper.generateGUID();
    residentialCredentialServiceProvider.getGroupId();
    var identity = residentialCredentialServiceProvider.getGroupId() + "+" + location.getCode().getCode() + "+" + guid;
    var password = authCommonService.getPassword();
    return new ProxyCredential(identity, password);
});
var residentialLocationConfiguration = new LocationConfiguration('Residential', false, true, 'images/main_shuffle.png', residentialCredentialServiceProvider);
var residentialLocationService = new LocationService(baseProxyService, residentialLocationConfiguration);
var dataCenterCredentialServiceProvider = new CredentialServiceProvider(function () {
    var identity = authCommonService.getIdentity();
    var password = authCommonService.getPassword();
    return new ProxyCredential(identity, password);
});
var dataCenterLocationConfiguration = new LocationConfiguration('Data Center', false, false, 'images/main-on.png', dataCenterCredentialServiceProvider);
var dataCenterLocationService = new LocationService(baseProxyService, dataCenterLocationConfiguration);
var syncService = new SyncService();
var dataCenterSyncProxyListService = new DataCenterSyncProxyListService(authCommonService, dataCenterLocationService, loggerService, locationMapper);
var residentialSyncProxyListService = new ResidentialSyncProxyListService(authCommonService, residentialLocationService, loggerService, locationMapper);
var locationCommonService = new LocationCommonService(locationHistoryStachStorage);
authCommonService.registerAuthService(AppConstant.AUTH_SERVICE.DATA_CENTER, dataCenterAuthService);
authCommonService.registerAuthService(AppConstant.AUTH_SERVICE.RESIDENTIAL, residentialAuthService);
syncService.registerJob(AppConstant.SYNC_SERVICE.DATA_CENTER, dataCenterSyncProxyListService);
syncService.registerJob(AppConstant.SYNC_SERVICE.RESIDENTIAL, residentialSyncProxyListService);
locationCommonService.registerLocationService(AppConstant.LOCATION_SERVICE.DATA_CENTER, dataCenterLocationService);
locationCommonService.registerLocationService(AppConstant.LOCATION_SERVICE.RESIDENTIAL, residentialLocationService);
locationCommonService.changeLocationService(AppConstant.LOCATION_SERVICE.DATA_CENTER);
var ipInfoService = new IpInfoService(loggerService);
var ipInfoMapper = new IpInfoMapper();
var geoService = new GeoService(ipInfoService, ipInfoMapper, tabService);
var core = new Core();
core.registerService(CoreConstant.SERVICE.LOGGER_SERVICE, loggerService);
core.registerService(CoreConstant.SERVICE.DISPATCHER_SERVICE, dispatcherService);
core.registerService(AppConstant.SERVICE.LOCATION_MAPPER_SERVICE, locationMapper);
core.registerService(AppConstant.SERVICE.LOCATION_SERVICE, locationCommonService);
core.registerService(AppConstant.SERVICE.SYNC_SERVICE, syncService);
core.registerService(AppConstant.SERVICE.AUTH_SERVICE, authCommonService);
// core.registerService(AppConstant.SERVICE.WEB_REQUESTS_SERVICE, webRequestsService);
core.registerService(AppConstant.SERVICE.AUTH_DATA_CENTER_SERVICE, dataCenterAuthService);
core.registerService(AppConstant.SERVICE.DEVICE_SERVICE, deviceService);
core.registerService(AppConstant.SERVICE.SETTINGS_SERVICE, settingsService);
core.registerService(AppConstant.SERVICE.CACHE_SERVICE, cacheService);
core.registerService(AppConstant.SERVICE.STATISTICS_SERVICE, statisticsService);
core.registerService(AppConstant.SERVICE.RESIDENTIAL_LOCATION_SERVICE, residentialLocationService);
core.registerService(AppConstant.SERVICE.DATA_CENTER_LOCATION_SERVICE, dataCenterLocationService);
core.registerService(AppConstant.SERVICE.BASE_PROXY_SERVICE, baseProxyService);
core.registerService(AppConstant.SERVICE.STORAGE_SERVICE, storageService);
core.registerService(AppConstant.SERVICE.TOOLBAR_ICON_SERVICE, toolbarIconService);
core.registerService(AppConstant.SERVICE.TAB_SERVICE, tabService);
core.registerService(AppConstant.SERVICE.TIMER_SERVICE, timerService);
core.registerService(AppConstant.SERVICE.MESSAGE_DISPATCH_SERVICE, messageDispatcherService);
core.registerService(AppConstant.SERVICE.ACCOUNT_STORAGE_SERVICE, accountStorage);
core.registerService(AppConstant.SERVICE.SETTINGS_STORAGE_SERVICE, settingsStorage);
core.registerService('residentialSyncProxyList', residentialSyncProxyListService);
core.registerService(AppConstant.SERVICE.IP_INFO_SERVICE, ipInfoService);
core.registerService(AppConstant.MAPPER.IP_INFO_MAPPER, ipInfoMapper);
core.registerService(AppConstant.SERVICE.GEO_SERVICE, geoService);
var globalAny = global;
globalAny.window.bgCore = core;
globalAny.window.locationCommonService = locationCommonService;
globalAny.window.authService = authCommonService;
globalAny.window.deviceService = deviceService;
globalAny.window.settingsService = settingsService;
globalAny.window.cacheService = cacheService;
globalAny.window.backgroundScript = core;
globalAny.window.statisticsService = statisticsService;
globalAny.window.residentialProxyService = residentialLocationService;
globalAny.window.dataCenterProxyService = dataCenterLocationService;
globalAny.window.tabService = tabService;
globalAny.window.dataCenterSyncProxyListService = dataCenterSyncProxyListService;
globalAny.window.loggerService = loggerService;
export default core;
