class StatisticsService {
  send(value) {
    $.ajax({
      url: 'http://toolbar.geosurf.com/toolbar/statistic.php',
      data: {
        cmd: value,
        uname: authService.getIdentity(),
      },
      type: 'POST',
      dataType: 'xml',
      beforeSend: function () {
        $.support.cors = true;
      }
    });
  }
}

StatisticsService.prototype.CLEAR_COOKIE_ON  = 'clear_cookie_on';
StatisticsService.prototype.CLEAR_COOKIE_OFF = 'clear_cookie_off';
StatisticsService.prototype.CLEAR_CACHE_OFF  = 'clear_cache_off';
StatisticsService.prototype.CLEAR_CACHE_ON   = 'clear_cache_on';

export default StatisticsService;