import tldjs from 'tldjs';
import validator from 'validator';

class UrlHelper {
  /**
   * a url should has a protocol https or http with domain name
   * @param url
   * @throws Error - when url is not valid
   * @return string
   */
  static parseGetProtocolWithDomainName(url) {
    url = url.trim();
    let data = tldjs.parse(url);
    if (!validator.isURL(url, { protocols: ['http','https'], require_protocol: true })) {
      throw new Error('Site name not valid');
    }
    const protocol = url.indexOf('https://') === 0 ? 'https://' : 'http://';
    const domain = protocol + data.hostname;
    return domain;
  }
}

export default UrlHelper;